import { useState, useEffect } from 'react';
import { useParams, useRouter } from 'next/navigation';
import { useSession } from 'next-auth/react';
import {
  Box, Button, TextField, Typography, CircularProgress, List, ListItem, ListItemText,
  IconButton, Dialog, DialogTitle, DialogContent, DialogActions
} from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import AddIcon from '@mui/icons-material/Add';
import  toast  from 'react-h';

interface FileReference {
  file_id: string;
  filename: string;
  content_type?: string;
}

interface FormDataType {
  companyID: string;
  name: string;
  description: string;
  context_texts: FileReference[];
  context_files: FileReference[];
  faqDICT: { [key: string]: string };
  helpers: string[];
}

type FaqItem = { id: string; question: string; answer: string };

export default function CompanyDetails() {
  const { username, companyid } = useParams();
  const router = useRouter();
  const { data: session } = useSession();
  const [company, setCompany] = useState<FormDataType | null>(null);
  const [formData, setFormData] = useState<FormDataType>({} as FormDataType);
  const [faqList, setFaqList] = useState<FaqItem[]>([]);
  const [originalFaqList, setOriginalFaqList] = useState<FaqItem[]>([]);
  const [newAgentEmail, setNewAgentEmail] = useState('');
  const [files, setFiles] = useState<File[]>([]);
  const [loading, setLoading] = useState(true);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);

  useEffect(() => {
    const fetchCompany = async () => {
      try {
        const response = await fetch(`http://localhost:8000/company/${companyid}`, {
          headers: { Authorization: `Bearer ${session?.accessToken}` },
        });
        if (!response.ok) throw new Error('Failed to fetch company');
        const data = await response.json();
        setCompany(data);
        setFormData(data);
        const initialFaqList = Object.entries(data.faqDICT).map(([question, answer], index) => ({
          id: `faq-${index}`,
          question,
          answer,
        }));
        setFaqList(initialFaqList);
        setOriginalFaqList(initialFaqList);
      } catch (error) {
        toast.error((error as Error).message);
      } finally {
        setLoading(false);
      }
    };
    fetchCompany();
  }, [companyid, session]);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

  const handleDeleteFile = (fileId: string, type: 'context_texts' | 'context_files') => {
    setFormData((prev) => ({
      ...prev,
      [type]: prev[type].filter((file) => file.file_id !== fileId),
    }));
  };

  const handleFaqChange = (id: string, field: 'question' | 'answer', value: string) => {
    setFaqList((prev) =>
      prev.map((item) => (item.id === id ? { ...item, [field]: value } : item))
    );
  };

  const handleRemoveFaq = (id: string) => {
    setFaqList((prev) => prev.filter((item) => item.id !== id));
  };

  const handleAddFaq = () => {
    const newId = `faq-${faqList.length}`;
    setFaqList((prev) => [...prev, { id: newId, question: '', answer: '' }]);
  };

  const handleRemoveAgent = (email: string) => {
    setFormData((prev) => ({
      ...prev,
      helpers: prev.helpers.filter((e) => e !== email),
    }));
  };

  const handleAddAgent = () => {
    if (newAgentEmail && !formData.helpers.includes(newAgentEmail)) {
      setFormData((prev) => ({
        ...prev,
        helpers: [...prev.helpers, newAgentEmail],
      }));
      setNewAgentEmail('');
    }
  };

  const handleUpdate = async () => {
    try {
      const updatedFaqDict = faqList.reduce((acc, item) => {
        if (item.question && item.answer) acc[item.question] = item.answer;
        return acc;
      }, {} as { [key: string]: string });

      const payload = { ...formData, faqDICT: updatedFaqDict };
      const response = await fetch(`http://localhost:8000/company/${companyid}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
          Authorization: `Bearer ${session?.accessToken}`,
        },
        body: JSON.stringify(payload),
      });
      if (!response.ok) throw new Error('Failed to update company');
      toast.success('Company updated successfully');
      const refreshedCompany = await fetch(`http://localhost:8000/company/${companyid}`, {
        headers: { Authorization: `Bearer ${session?.accessToken}` },
      }).then((res) => res.json());
      setCompany(refreshedCompany);
      setFormData(refreshedCompany);
      setFaqList(
        Object.entries(refreshedCompany.faqDICT).map(([q, a], i) => ({ id: `faq-${i}`, question: q, answer: a }))
      );
      setOriginalFaqList(
        Object.entries(refreshedCompany.faqDICT).map(([q, a], i) => ({ id: `faq-${i}`, question: q, answer: a }))
      );
    } catch (error) {
      toast.error((error as Error).message);
    }
  };

  const handleReset = () => {
    if (company) {
      setFormData(company);
      setFaqList(originalFaqList);
    }
  };

  const handleDelete = async () => {
    try {
      const response = await fetch(`http://localhost:8000/company/${companyid}`, {
        method: 'DELETE',
        headers: { Authorization: `Bearer ${session?.accessToken}` },
      });
      if (!response.ok) throw new Error('Failed to delete company');
      toast.success('Company deleted successfully');
      router.push(`/admin/${username}`);
    } catch (error) {
      toast.error((error as Error).message);
    } finally {
      setDeleteDialogOpen(false);
    }
  };

  const handleFileUpload = async () => {
    if (files.length === 0) return;
    const formDataFiles = new FormData();
    files.forEach((file) => formDataFiles.append('files', file));
    try {
      const response = await fetch(`http://localhost:8000/company/${companyid}/add_files`, {
        method: 'POST',
        headers: { Authorization: `Bearer ${session?.accessToken}` },
        body: formDataFiles,
      });
      if (!response.ok) throw new Error('Failed to upload files');
      toast.success('Files uploaded successfully');
      setFiles([]);
      const refreshedCompany = await fetch(`http://localhost:8000/company/${companyid}`, {
        headers: { Authorization: `Bearer ${session?.accessToken}` },
      }).then((res) => res.json());
      setCompany(refreshedCompany);
      setFormData(refreshedCompany);
      setFaqList(
        Object.entries(refreshedCompany.faqDICT).map(([q, a], i) => ({ id: `faq-${i}`, question: q, answer: a }))
      );
      setOriginalFaqList(
        Object.entries(refreshedCompany.faqDICT).map(([q, a], i) => ({ id: `faq-${i}`, question: q, answer: a }))
      );
    } catch (error) {
      toast.error('Error uploading files');
    }
  };

  const hasChanges =
    JSON.stringify(formData) !== JSON.stringify(company) ||
    JSON.stringify(faqList) !== JSON.stringify(originalFaqList);

  if (loading) return <CircularProgress sx={{ display: 'block', mx: 'auto', mt: 4 }} />;

  return (
    <Box sx={{ p: 4, maxWidth: 800, mx: 'auto' }}>
      <Typography variant="h4" gutterBottom>Company Details</Typography>
      
      {/* Basic Information */}
      <TextField
        label="Company ID"
        name="companyID"
        value={formData.companyID || ''}
        disabled
        fullWidth
        margin="normal"
        helperText="Company ID cannot be changed after creation"
      />
      <TextField
        label="Name"
        name="name"
        value={formData.name || ''}
        onChange={handleInputChange}
        fullWidth
        margin="normal"
      />
      <TextField
        label="Description"
        name="description"
        value={formData.description || ''}
        onChange={handleInputChange}
        fullWidth
        margin="normal"
        multiline
        rows={4}
      />

      {/* Context Texts */}
      <Typography variant="h6" sx={{ mt: 2 }}>Context Texts</Typography>
      <List>
        {formData.context_texts.map((file) => (
          <ListItem key={file.file_id} sx={{ display: 'flex', justifyContent: 'space-between' }}>
            <ListItemText primary={file.filename} />
            <IconButton onClick={() => handleDeleteFile(file.file_id, 'context_texts')}>
              <CloseIcon />
            </IconButton>
          </ListItem>
        ))}
      </List>

      {/* Context Files */}
      <Typography variant="h6" sx={{ mt: 2 }}>Context Files</Typography>
      <List>
        {formData.context_files.map((file) => (
          <ListItem key={file.file_id} sx={{ display: 'flex', justifyContent: 'space-between' }}>
            <ListItemText primary={file.filename} />
            <IconButton onClick={() => handleDeleteFile(file.file_id, 'context_files')}>
              <CloseIcon />
            </IconButton>
          </ListItem>
        ))}
      </List>

      {/* File Upload */}
      <Box sx={{ mt: 2 }}>
        <input
          type="file"
          multiple
          onChange={(e) => setFiles(Array.from(e.target.files || []))}
          style={{ display: 'block', marginBottom: '10px' }}
        />
        <Button
          variant="contained"
          onClick={handleFileUpload}
          disabled={files.length === 0}
          sx={{ bgcolor: '#1976d2', '&:hover': { bgcolor: '#1565c0' } }}
        >
          Upload Files
        </Button>
      </Box>

      {/* FAQs */}
      <Typography variant="h6" sx={{ mt: 2 }}>FAQs</Typography>
      {faqList.map((faqItem) => (
        <Box key={faqItem.id} sx={{ display: 'flex', gap: 2, mb: 2, alignItems: 'center' }}>
          <TextField
            label="Question"
            value={faqItem.question}
            onChange={(e) => handleFaqChange(faqItem.id, 'question', e.target.value)}
            fullWidth
          />
          <TextField
            label="Answer"
            value={faqItem.answer}
            onChange={(e) => handleFaqChange(faqItem.id, 'answer', e.target.value)}
            fullWidth
          />
          <IconButton onClick={() => handleRemoveFaq(faqItem.id)}>
            <CloseIcon />
          </IconButton>
        </Box>
      ))}
      <Button
        startIcon={<AddIcon />}
        onClick={handleAddFaq}
        sx={{ bgcolor: '#1976d2', color: 'white', '&:hover': { bgcolor: '#1565c0' } }}
      >
        Add FAQ
      </Button>

      {/* Agents */}
      <Typography variant="h6" sx={{ mt: 2 }}>Agents</Typography>
      <List>
        {formData.helpers.map((email, index) => (
          <ListItem key={index} sx={{ display: 'flex', justifyContent: 'space-between' }}>
            <ListItemText primary={email} />
            <IconButton onClick={() => handleRemoveAgent(email)}>
              <CloseIcon />
            </IconButton>
          </ListItem>
        ))}
      </List>
      <Box sx={{ display: 'flex', gap: 2, mt: 2 }}>
        <TextField
          label="New Agent Email"
          value={newAgentEmail}
          onChange={(e) => setNewAgentEmail(e.target.value)}
          fullWidth
        />
        <Button
          variant="contained"
          onClick={handleAddAgent}
          sx={{ bgcolor: '#1976d2', '&:hover': { bgcolor: '#1565c0' } }}
        >
          Add Agent
        </Button>
      </Box>

      {/* Action Buttons */}
      <Box sx={{ mt: 4 }}>
        <Button
          variant="contained"
          onClick={handleUpdate}
          sx={{ mr: 1, bgcolor: '#1976d2', '&:hover': { bgcolor: '#1565c0' } }}
        >
          Update
        </Button>
        <Button
          variant="outlined"
          onClick={handleReset}
          disabled={!hasChanges}
          sx={{ mr: 1 }}
        >
          Reset
        </Button>
        <Button
          variant="contained"
          color="error"
          onClick={() => setDeleteDialogOpen(true)}
        >
          Delete
        </Button>
      </Box>

      {/* Delete Confirmation Dialog */}
      <Dialog open={deleteDialogOpen} onClose={() => setDeleteDialogOpen(false)}>
        <DialogTitle>Confirm Delete</DialogTitle>
        <DialogContent>Are you sure you want to delete this company?</DialogContent>
        <DialogActions>
          <Button onClick={() => setDeleteDialogOpen(false)}>Cancel</Button>
          <Button onClick={handleDelete} color="error">Delete</Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
}